import * as XLSX from 'xlsx';
import VentasFiltro from './VentasFiltro';

const VentasTable = () => {
  const { ventas, eliminarVenta, editarVenta } = useVentas();
  const [filtros, setFiltros] = useState({
    cliente: '',
    fechaInicio: '',
    fechaFin: '',
    montoMin: '',
    montoMax: '',
  });

  const ventasFiltradas = ventas.filter(v => {
    const nombre = v.cliente.nombre.toLowerCase();
    const clienteOK = nombre.includes(filtros.cliente.toLowerCase());
    const fecha = new Date(v.fechaRegistro).getTime();
    const desde = filtros.fechaInicio ? new Date(filtros.fechaInicio).getTime() : null;
    const hasta = filtros.fechaFin ? new Date(filtros.fechaFin).getTime() : null;
    const montoMin = filtros.montoMin ? parseFloat(filtros.montoMin) : null;
    const montoMax = filtros.montoMax ? parseFloat(filtros.montoMax) : null;

    const fechaOK = (!desde || fecha >= desde) && (!hasta || fecha <= hasta);
    const montoOK = (!montoMin || v.costoTotal >= montoMin) && (!montoMax || v.costoTotal <= montoMax);

    return clienteOK && fechaOK && montoOK;
  });

  const exportarExcel = () => {
    const data = ventasFiltradas.map(v => ({
      ID: v.idVentaServicio,
      Cliente: v.cliente.nombre,
      Correo: v.cliente.correo,
      Fecha: formatDate(v.fechaRegistro),
      Servicios: v.detalles.length,
      Total: v.costoTotal,
    }));

    const wb = XLSX.utils.book_new();
    const ws = XLSX.utils.json_to_sheet(data);
    XLSX.utils.book_append_sheet(wb, ws, "Ventas");
    XLSX.writeFile(wb, "ventas.xlsx");
  };

  return (
    <>
      <VentasFiltro filtros={filtros} onFiltroChange={setFiltros} />

      <div className="flex justify-end mb-2">
        <button onClick={exportarExcel} className="bg-green-600 text-white px-4 py-2 rounded-md">
          📤 Exportar a Excel
        </button>
      </div>

      {/* tabla igual que antes pero con ventasFiltradas.map(...) */}
    </>
  );
};
